export const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);
    return {
        div, slider, text
    }
};
export const getCheckBox = (title: string) => {
    const div = document.createElement('div');
    div.style.display = 'flex';

    const checkBox = document.createElement('input');
    checkBox.type = 'checkbox';
    checkBox.defaultChecked = false;
    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(checkBox);
    div.appendChild(text);

    return {
        div, checkBox
    }
};




export const getDropdown = (items = ["Link 1", "Link 2", "Link 3"], callBack: (param: string)=> void) => {

    const dropdown = document.createElement('div');
    dropdown.classList.add('dropdown');

    const dropdownToggle = document.createElement('button');
    dropdownToggle.classList.add('dropdown-toggle');
    dropdownToggle.textContent = 'Dropdown Menu';
    dropdown.appendChild(dropdownToggle);

    const dropdownMenu = document.createElement('div');
    dropdownMenu.classList.add('dropdown-menu');
    dropdown.appendChild(dropdownMenu);

    items.forEach(item => {
        const anchor = document.createElement('a');
        anchor.href = "#";
        anchor.textContent = item;
        anchor.addEventListener('click', (event) => {
            event.preventDefault();
            // console.log(`Selected: ${item}`);
            dropdownToggle.textContent = `Selected: ${item}`
            dropdownMenu.style.display = 'none';
            callBack(item);
        });
        dropdownMenu.appendChild(anchor);
    });



    dropdownToggle.addEventListener('click', () => {
        dropdownMenu.style.display = dropdownMenu.style.display === 'block' ? 'none' : 'block';
    });

    window.addEventListener('click', (event) => {
        // @ts-expect-error
        if (!event.target.matches('.dropdown-toggle')) {
            if (dropdownMenu.style.display === 'block') {
                dropdownMenu.style.display = 'none';
            }
        }
    });

    document.body.style.fontFamily = 'Arial, sans-serif';

    dropdown.style.position = 'relative';
    dropdown.style.display = 'inline-block';

    // dropdownToggle.style.backgroundColor = '#3498db';
    // dropdownToggle.style.color = 'white';
    dropdownToggle.style.padding = '10px 20px';
    dropdownToggle.style.border = 'none';
    dropdownToggle.style.cursor = 'pointer';

    dropdownToggle.addEventListener('mouseover', () => {
        dropdownToggle.style.backgroundColor = '#eee';
    });

    dropdownToggle.addEventListener('mouseout', () => {
        dropdownToggle.style.backgroundColor = '#ccc';
    });

    dropdownMenu.style.display = 'none';
    dropdownMenu.style.position = 'absolute';
    dropdownMenu.style.backgroundColor = '#ccc';
    dropdownMenu.style.boxShadow = '0px 8px 16px 0px rgba(0,0,0,0.2)';
    dropdownMenu.style.zIndex = '1';

    dropdownMenu.querySelectorAll('a').forEach(anchor => {
        anchor.style.color = 'black';
        anchor.style.padding = '12px 16px';
        anchor.style.textDecoration = 'none';
        anchor.style.display = 'block';

        anchor.addEventListener('mouseover', () => {
            anchor.style.backgroundColor = '#f1f1f1';
        });

        anchor.addEventListener('mouseout', () => {
            anchor.style.backgroundColor = '#ccc';
        });
    });

    // container.appendChild(dropdown);
    return dropdown;
};