import { Vector3 } from '../';
import * as THREE from 'three';
import { NRenderer } from '..';

export class Arrow {
    public vec: Vector3;
    public dir: Vector3;
    public meshArrow: THREE.ArrowHelper;
    constructor(vec: Vector3, dir: Vector3) {
        this.vec = vec;
        this.dir = dir;
        this.init();
    }
    public onClick(data: any) {
        console.log(data)
        /* */
    }
    public update() {
        this.meshArrow.position.x = this.vec.x;
        this.meshArrow.position.y = this.vec.y;
        this.meshArrow.position.z = this.vec.z;
        
        const dir = new THREE.Vector3(this.dir.x, this.dir.y, this.dir.z);
        const len = dir.length();
        
        this.meshArrow.setLength(len, 0.1,  0.1);
        this.meshArrow.setDirection(dir.normalize());
    }
    private init() {
        const dir = new THREE.Vector3(this.dir.x, this.dir.y, this.dir.z);
        const len = dir.length();

        this.meshArrow = new THREE.ArrowHelper(
            dir.normalize(), 
            new THREE.Vector3(this.vec.x, this.vec.y, this.vec.z), 
            len, '#f00'
        );

        // console.log(len)
        // this.meshArrow.setLength(len, 0.1,  0.1)
        // this.meshArrow.setDirection(dir);
        // this.arrayArrow.push(arrowHelper)

        this.meshArrow.userData = this;
        // @ts-ignore
        NRenderer.app.scene.add(this.meshArrow);
    }
}